.hat {
  position: fixed;
  width: 100%;
  height: 12vh;
  background-color: #8e24aa;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

#box {
  opacity: 0;
}

.hatContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.btn {
  left: 0px;
  position: relative;
  height: 30px;
  width: 30px;
  cursor: pointer;
  bottom: 20px;
  left: 10px;
}
.btn > span,
.btn > span::before,
.btn > span::after {
  position: absolute;
  background-color: white;
  width: 100%;
  height: 2px;
}

.btn > span::before {
  content: '';
  top: -8px;
}

.btn > span::after {
  content: '';
  top: 8px;
}

.list {
  width: 25vw;
  height: 100vh;
  background-color: rgb(142, 36, 170, 0.9);
  display: flex;
  justify-content: space-evently;
  top: 10.1vh;
  position: fixed;
  flex-direction: column;
  left: 0;
}

