.items {
  padding: 10px;
  text-align: start;
  color: #fff;
  font-size: 25px;
}

.Name {
  color: white;
  margin-left: 30px;
}

.search {
  border: 0;
  height: 60px;
  width: 60px;
  background: #8e24aa;
  cursor: pointer;
  margin-right: 10px;
}
.search:focus {
  outline: 0;
}

.circle {
  position: relative;
  width: 20px;
  border: 3px #fff solid;
  border-radius: 100%;
  margin-top: 10px;
  margin-left: 6px;
  content: '';
  height: 20px;
  display: block;
}

.handle {
  position: relative;
  height: 16px;
  width: 4px;
  left: 30px;
  bottom: 7px;
  background-color: #fff;
  transform: rotate(-45deg);
}

.searchInput {
  flex-grow: 1;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-left: 10px;
}

input {
  height: 30px;
  font-size: 20px;
  display: block;
}
